
body{
    font-weight: bolder;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

.row{
    /* border: 1px solid black; */
    margin: 2% auto;
    width: 90%;
}

.col-md-4{
    /* border: 1px solid black; */
    margin: 2% auto;
}

.card{
    border: 1px solid black;
    margin: 0px auto;
    width: 100%;
}

.card img{
    /* border: 1px solid black; */
    width: 92%;
    margin-left: 4%;
    margin-top: 4%;
}

.container h2{
    text-align: center;
    font-size: 45px;
}

.container .input-group{
    width: 50%;
    margin: 5% auto;
    height: 60px;
}

.input-group input{
    font-weight: bolder;
    font-size: 20px;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

.input-group input:hover{
    border: 2px solid black;
}

.btn{
    color: black;
    font-weight: bolder;
}

.btn:hover{
    border: 2px solid black;
    background-color: white;
    color: black;
}

/* ------------------ for mobile screens -------------------- */
@media(min-width: 300px){

    .container h2{
        margin-top: 3%;
        font-size: 25px;
        text-align: center;
    }

    .container .row{
        margin-top: 10%;
    }

    .card{
        margin-top: 10%;
    }

    .container .line{
        width: 50%;
        height: 3px;
        background-color: black;
        margin: 1% auto;
    }

    .container .input-group{
        width: 98%;
        margin: 5% auto;
    }
}
/* ------------------ for tablet screens -------------------- */
@media(min-width: 550px){

    .container h2{
        /* margin-top: 10%; */
        font-size: 35px;
        text-align: center;
    }

    .container .row{
        margin-top: 10%;
    }

    .card{
        margin-top: 10%;
    }

    .container .line{
        width: 60%;
        height: 3px;
        background-color: black;
        margin: 1% auto;
    }

    .container .input-group{
        width: 98%;
        margin: 5% auto;
    }
}
/* ------------------ for laptop screens -------------------- */
@media(min-width: 900px){

    .container h2{
        font-size: 45px;
        text-align: center;
    }

    .container .row{
        margin-top: 2%;
    }

    .card{
        margin-top: 4%;
    }

    .container .line{
        width: 70%;
        height: 3px;
        background-color: black;
        margin: 1% auto;
    }

    .container .input-group{
        width: 60%;
        margin: 5% auto;
    }
}